.list-item {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	-ms-flex-align: center;
	    align-items: center;
	padding: 10px;
	background-color: transparent;

	-webkit-transition: background-color .3s;

	-o-transition: background-color .3s;

	transition: background-color .3s;
}
.list-item:hover {
	background-color: #f8f8f8;
	cursor: pointer;
}

.list-item-image {
	margin-right: 15px;
	font-size: 18px;
	width: 1.25em !important;
}

.list-item-content {
	-ms-flex-positive: 1;
	    flex-grow: 1;
}

.list-item-title {
	font-weight: 400;
    font-size: 14px;
}

.list-item-description {
	margin-top: 3px;
    font-size: 13px;
}


